.footer{flex-shrink:0;width:100%;height:5vh;background-color:whitesmoke;display:flex;justify-content:space-between;align-items:center;padding:0 20px}.footer a{width:-webkit-max-content;width:max-content;text-align:center;text-decoration:none;color:#8f9399;font-size:1.1rem;font-weight:300;text-transform:capitalize;transition:all 300ms ease}.footer a:hover{color:#4ebba0;font-weight:500}.footer p{color:#8f9399;font-size:1.1rem;font-weight:300}

